@import 'components/variables';
@import "components/generales/accordion";
@import 'components/generales/banner-dudas.scss';

.section-1,
.section-7 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
  li {
    color: $gray-siroco;
    span {
      font-weight: 800;
    }
  }
  .text-mapa {
    margin-bottom: 5rem;
  }
  .title-mapa {
    margin-left: 1rem;
    border-bottom: 1px solid #20A9D7;
    color: $gray-siroco;
    font-weight: 800;
    width: 100%;
  }
  .table {
    color: $white;
    overflow-y: hidden;
    @media (max-width: 424.98px) {
      font-size: .8rem;
    }
  }
  .tabla1 {
    background-color: $blue-tabla1;
    border-radius: 10px;
  }
  .tabla2 {
    background-color: $blue-tabla2;
  }
  .tabla3 {
    background-color: $blue-tabla3;
    border-radius: 10px;
  }
  .tabla4 {
    background-color: $blue-tabla4;
  }
  .tabla5 {
    background-color: $blue-tabla4;
    border-radius: 10px;
  }
  .tabla6 {
    background-color: $blue-tabla2;
    border-radius: 10px;
  }
  .line-material {
    width: 6.5rem;
    height: .6rem;
    background-color: $blue-tabla4;
    opacity: 1;
  }
  .line-map-cards {
    width: 8.5rem;
    height: .2rem;
    background-color: $blue-tabla4;
    opacity: 1;
    margin: 0; 
  }
}
.section-2,
.section-4,
.section-6,
.section-8,
.section-10,
.section-12 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $gray-claro;
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
}
.section-3 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  li {
    color: $gray-siroco;
    span {
      font-weight: 800;
    }
  }
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
}
.section-5,
.section-9,
.section-11,
.section-13 {
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.title-cards {
  h4 {
    color: $blue-siroco;
    font-family: Montserrat;
    font-weight: bold;
    margin-bottom: 1rem;
    transition: all .5s;
    &:hover {
      color: $blue-claro;
    }
    span {
      color: $blue-siroco;
      font-family: Montserrat;
      font-weight: 400;
      transition: all .5s;
      &:hover {
        color: $blue-claro;
      }
    }
  }
}
.img-cards-guia {
  max-width: 70%;
  opacity: 0.8;
  box-shadow: 10px 14px 4px -2px rgba(#DAF1FA, $alpha: .75);
  border-radius: 15px;
  transition: all .5s;
  &:hover {
    transform: scale(1.1);
    opacity: 1;
  }
}